'use client';

import { useState } from 'react';

export default function BusinessInquiryForm() {
  const [formData, setFormData] = useState({
    businessName: '',
    fullName: '',
    email: '',
    phone: '',
    submissionDate: '19-07-2025',
    interestMain: '',
    interestSecondary: '',
    businessSize: '',
    yearsInBusiness: '',
    businessLinks: '',
    inquiryDetails: '',
    budget: '',
    callTime: '',
    referral: '',
  });

  const [charCount, setCharCount] = useState(0);

  const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement | HTMLSelectElement>) => {
    const { name, value } = e.target;
    setFormData({ ...formData, [name]: value });

    if (name === 'inquiryDetails') setCharCount(value.length);
  };

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    console.log(formData);
    alert('Form submitted! Check console.');
  };

  return (
    <div className="min-h-screen bg-gray-100 flex items-center justify-center py-12 px-4 sm:px-6 lg:px-8">
      <form
        onSubmit={handleSubmit}
        className="max-w-3xl w-full bg-white shadow-xl rounded-2xl p-8 space-y-6"
      >
        <div className="text-center">
          <h2 className="text-3xl font-bold text-gray-800">Let our business help yours.</h2>
          <p className="text-gray-500">We want to hear from you.</p>
          <p className="text-sm text-red-500 italic mt-2">
            P.S. If you are applying for a job position, your application will not be considered if you use this form.
          </p>
        </div>

        {/* Input Fields */}
        <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
          <Input label="Name of Business" name="businessName" required onChange={handleChange} />
          <Input label="First & Last Name" name="fullName" required onChange={handleChange} />
          <Input type="email" label="Email" name="email" required onChange={handleChange} />
          <Input type="tel" label="Phone" name="phone" required onChange={handleChange} />
          <Input label="Date of Submission" name="submissionDate" value={formData.submissionDate} readOnly />
          <Input label="I am mostly interested in..." name="interestMain" required onChange={handleChange} />
          <Input label="I am also interested in..." name="interestSecondary" required onChange={handleChange} />
          <Input label="Business Size" name="businessSize" required onChange={handleChange} />
          <Input label="Years in Business" name="yearsInBusiness" onChange={handleChange} />
          <Input
            label="Business Links"
            name="businessLinks"
            required
            placeholder="Website and Instagram, or NA"
            onChange={handleChange}
          />
          <Input label="Budget (AED)" name="budget" placeholder="Monthly or per project" onChange={handleChange} />
          <Input
            label="What is the best time to call you?"
            name="callTime"
            required
            onChange={handleChange}
          />
          <Input
            label="Where did you hear about us?"
            name="referral"
            required
            onChange={handleChange}
          />
        </div>

        {/* Inquiry Details */}
        <div>
          <label className="block text-gray-700 font-semibold mb-1">Inquiry Details *</label>
          <textarea
            name="inquiryDetails"
            maxLength={2000}
            required
            rows={5}
            onChange={handleChange}
            className="w-full border border-gray-300 rounded-md p-3 focus:outline-none focus:ring-2 focus:ring-blue-500"
          />
          <p className="text-sm text-gray-500 text-right">{charCount}/2000 characters</p>
        </div>

        <button
          type="submit"
          className="w-full bg-black text-white py-3 rounded-md hover:bg-gray-800 transition-all duration-200 font-semibold"
        >
          Submit Inquiry
        </button>
      </form>
    </div>
  );
}

// Reusable Input component
const Input = ({
  label,
  name,
  type = 'text',
  required = false,
  placeholder = '',
  value = '',
  readOnly = false,
  onChange,
}: {
  label: string;
  name: string;
  type?: string;
  required?: boolean;
  placeholder?: string;
  value?: string;
  readOnly?: boolean;
  onChange?: (e: React.ChangeEvent<HTMLInputElement>) => void;
}) => (
  <div className="flex flex-col">
    <label htmlFor={name} className="text-gray-700 font-semibold mb-1">
      {label} {required && '*'}
    </label>
    <input
      type={type}
      id={name}
      name={name}
      required={required}
      placeholder={placeholder}
      value={value}
      readOnly={readOnly}
      onChange={onChange}
      className={`p-3 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500 ${
        readOnly ? 'bg-gray-100 cursor-not-allowed' : ''
      }`}
    />
  </div>
);
